<template>
  <el-row :gutter="10" type="flex" justify="center">
    <el-col :offset="1" :xs="22" :sm="22" :md="16" :lg="16" :xl="16">
      <el-row type="flex" class="text-color-gray">
        <el-col :span="12">
          <label style="padding-left: 10px">Editar:</label>
          <el-switch v-model="isUpdateForm"></el-switch>
        </el-col>
      </el-row>
      <div v-if="!isUpdateForm" style="margin: 20px">
        <!-- Carde title -->
        <el-card class="box-card">
          <div class="text item">
            <label class="text-color-gray"
              >Nombre: {{ siteStore.settings.name }}</label
            ><br />
            <label class="text-color-gray"
              >URL base: {{ siteStore.settings.urlBase }}</label
            ><br />
            <label class="text-color-gray"
              >Lang: {{ siteStore.settings.lang }}</label
            ><br />
            <label class="text-color-gray"
              >Robots.txt:
              {{
                siteStore.settings.robots ? 'Activado' : 'Desactivado'
              }}</label
            ><br />
            <label class="text-color-gray">Favicon: </label><br />
            <img
              v-if="siteStore.settings.favicon"
              :src="configStore.baseUrl + siteStore.settings.favicon"
              alt="favicon"
            />
          </div>
        </el-card>
        <!-- End cart title -->
      </div>
      <div v-if="isUpdateForm" style="margin: 20px">
        <el-card class="box-card">
          <!-- Form -->
          <ValidationObserver ref="form" v-slot="{ invalid }">
            <el-form
              label-position="right"
              label-width="100px"
              :model="siteStore.settings"
            >
              <el-form-item label="Nombre">
                <ValidationProvider
                  name="nombre"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <el-input
                    size="mini"
                    v-model="siteStore.settings.name"
                  ></el-input>
                  <span>{{ errors[0] }}</span>
                </ValidationProvider>
              </el-form-item>
              <el-form-item label="URL base">
                <el-input size="mini" v-model="siteStore.settings.urlBase">
                  <template slot="prepend">Https://</template>
                </el-input>
              </el-form-item>
              <!-- Select Lang -->
              <el-form-item label="Idioma">
                <ValidationProvider
                  name="idioma"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <el-select
                    size="mini"
                    v-model="siteStore.settings.lang"
                    filterable
                    allow-create
                    placeholder="es-MX"
                  >
                    <el-option
                      v-for="itemL in siteStore.optionsLang"
                      :key="itemL.value"
                      :label="itemL.label"
                      :value="itemL.value"
                    >
                    </el-option>
                  </el-select>
                  <span>{{ errors[0] }}</span>
                </ValidationProvider>
                <!-- End select -->
              </el-form-item>
              <el-form-item label="Robots.txt">
                <el-switch
                  v-model="siteStore.settings.robots"
                  active-text="Activar"
                  inactive-text="Desactivar"
                >
                </el-switch>
              </el-form-item>
              <el-form-item label="Favicon">
                <!-- Uploader -->
                <el-upload
                  class="favicon-uploader"
                  drag
                  name="favicon"
                  action="#"
                  :show-file-list="false"
                  :http-request="uploadImage"
                >
                  <img
                    v-if="siteStore.settings.favicon"
                    :src="configStore.baseUrl + siteStore.settings.favicon"
                    alt=""
                  />
                  <template v-else>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">
                      Suelta tu foto aquí o
                      <em>haz clic para cargar</em>
                    </div>
                  </template>
                  <div slot="tip" class="el-upload__tip">
                    Solo archivos ico/png con una medida de 400x400 pixeles
                  </div>
                </el-upload>
                <!-- End uploader -->
              </el-form-item>

              <!-- Butons action -->
              <el-form-item>
                <el-button
                  @click.stop.prevent="handleCancel"
                  type="danger"
                  size="mini"
                >
                  Cancelar</el-button
                >
                <el-button
                  @click.stop.prevent="checkSave"
                  type="primary"
                  size="mini"
                  :disabled="invalid"
                >
                  Actualizar</el-button
                >
              </el-form-item>
              <!-- End button -->
            </el-form>
          </ValidationObserver>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { store as storeImageFile } from '@/api/images/index'
import { useStoreSite } from '../../../stores/siteStores'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const siteStore = useStoreSite()
const configStore = useStoreConfig()

export default {
  name: 'Settings',
  data () {
    return {
      isUpdateForm: false,
      valueLang: '',
      siteStore,
      configStore
    }
  },
  async created () {
    siteStore.fetchSettings()
  },
  methods: {
    async update () {
      const settingsData = _.cloneDeep(siteStore.settings)
      siteStore.updateSettings(settingsData)
    },
    async uploadImage (param) {
      const formData = new FormData()
      formData.append('image', param.file)
      formData.append('width', '100')
      formData.append('height', '100')
      formData.append('title', 'Image')
      formData.append('alt', 'favicaon')
      formData.append('folder', 'favicon')
      storeImageFile(formData)
        .then((response) => {
          siteStore.settings.favicon = response.data.path
          param.onSuccess(response.data.path)
        })
        .catch((response) => {
          // console.log(response)
          param.onError()
        })
    },
    async checkSave () {
      const settingsData = _.cloneDeep(siteStore.settings)
      await siteStore
        .updateSettings(settingsData)
        .finally(() => siteStore.fetchSettings())

      this.isUpdateForm = false
    },
    handleCancel () {
      this.isUpdateForm = false
    }
  }
}
</script>
<style scoped>
.text-color-gray {
  color: #606266;
}
.image-seo {
  width: 100px;
  height: 100px;
}
.btn-js {
  background-color: #fff458;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
.btn-js:hover {
  background-color: #f0e985;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
</style>
