<template>
  <div class="app-container">
    <el-row :gutter="10">
      <el-col :offset="1" :xs="22" :sm="22" :md="22" :lg="22" :xl="22">
        <el-tabs type="border-card" @tab-click="handleClick">
          <el-tab-pane label="Configuración">
            <settings />
          </el-tab-pane>
          <el-tab-pane label="SEO">
            <seo />
          </el-tab-pane>
          <el-tab-pane label="CSS">
            <css />
          </el-tab-pane>
          <el-tab-pane label="JavaScript">
            <javascript />
          </el-tab-pane>
          <el-tab-pane label="Assets">
            <assets />
          </el-tab-pane>
          <!-- Custom CSS -->
          <el-row
            :gutter="10"
            v-loading="siteStore.loading"
            type="flex"
            justify="center"
            v-if="isPage"
          >
            <el-col :xs="23" :sm="23" :md="23" :lg="23" :xl="23">
              <el-card class="box-card">
                <div slot="header" class="clearfix">
                  <span>Custom</span>
                </div>
                <div>
                  <codemirror
                    ref="editorJavascript"
                    v-model="content"
                    :options="cmOptions"
                  />
                </div>
                <div class="margin-10">
                  <el-button
                    @click.stop.prevent="saveCustom"
                    type="primary"
                    size="small"
                  >
                    Actualizar</el-button
                  >
                </div>
              </el-card>
            </el-col>
          </el-row>
          <!-- End Custom CSS -->
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { useStoreSite } from '../../stores/siteStores'
import { codemirror } from 'vue-codemirror'
import _ from 'lodash'

import settings from './tabs/settings.vue'
import seo from './tabs/seo.vue'
import css from './tabs/css.vue'
import javascript from './tabs/javascript.vue'
import assets from './tabs/assets.vue'

const siteStore = useStoreSite()
var tabSelected

export default {
  components: {
    settings,
    seo,
    css,
    javascript,
    assets,
    codemirror
  },
  data () {
    return {
      loading: false,
      siteStore,
      isPage: false,
      content: '',
      cmOptions: {
        tabSize: 4,
        mode: '',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  created () {},
  mounted () {},
  methods: {
    handleClick (tab, event) {
      if (tab.$el.id === 'pane-2') {
        this.isPage = true
        this.cmOptions.mode = 'text/css'
        this.content = siteStore.customContentCss
        tabSelected = tab.$el.id
      } else if (tab.$el.id === 'pane-3') {
        this.isPage = true
        this.cmOptions.mode = 'text/javascript'
        this.content = siteStore.customContentJavascript
        tabSelected = tab.$el.id
      } else {
        this.isPage = false
      }
    },
    async saveCustom () {
      if (tabSelected === 'pane-2') {
        const contentData = _.cloneDeep(this.content)
        await siteStore.updateCustomCss(contentData)
      } else if (tabSelected === 'pane-3') {
        const contentData = _.cloneDeep(this.content)
        await siteStore.updateCustomJavascript(contentData)
      }
    }
  }
}
</script>
