<template>
  <div>
    <el-row
      :gutter="10"
      v-loading="siteStore.loading"
      element-loading-text="Cargando..."
      type="flex"
      justify="center"
    >
      <el-col :xs="23" :sm="23" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Agrega archivos a tu sitio</span>
          </div>

          <div class="text item">
            <label>Arrastra tu archivo css para agregarlo al sitio</label><br />

            <!-- Uploader -->
            <el-upload
              name="fileCSS"
              action="#"
              class="upload-demo"
              :show-file-list="false"
              :http-request="uploadFile"
              drag
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                Suelta tu archivo aquí o <em>haz clic para cargar</em>
              </div>
              <div slot="tip" class="el-upload__tip">
                Solo archivos CSS con un tamaño menor de 10 MB
              </div>
            </el-upload>
            <!-- End Uploader -->
          </div>

          <el-divider></el-divider>

          <div>
            <!-- Add CDN -->
            <label>Agrega url CDN par agregarla al sitio</label><br /><br />
            <el-row :gutter="10">
              <el-col :span="19">
                <el-input placeholder="Url CDN" v-model="cdnUrl"></el-input>
              </el-col>
              <el-col :span="2">
                <el-button type="success" @click="uploadCDN">
                  Agregar CDN
                </el-button>
              </el-col>
            </el-row>
          </div>
          <!-- End CDN -->
        </el-card>
      </el-col>

      <el-col :xs="23" :sm="23" :md="11" :lg="11" :xl="11">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Archivos cargados en el sitio</span>
          </div>
          <div class="text item">
            <div class="custom-tree-container">
              <div class="block">
                <!-- Start Tree -->
                <el-tree
                  :data="siteStore.css"
                  node-key="id"
                  :props="defaultProps"
                  default-expand-all
                  @node-drop="handleDrop"
                  draggable
                  :allow-drop="allowDrop"
                >
                  <span class="custom-tree-node" slot-scope="{ node, data }">
                    <span>{{ node.label }}</span>
                    <span>
                      <el-button
                        type="text"
                        size="mini"
                        @click="() => deleteFile(node, data)"
                      >
                        Eliminar
                      </el-button>
                    </span>
                  </span> </el-tree
                ><br />
                <!-- End Tree -->
                <div align="right">
                  <el-button
                    type="primary"
                    size="medium"
                    aling="right"
                    @click="updateTree()"
                  >
                    Actualizar
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { useStoreSite } from '../../../stores/siteStores'
import { upload } from '@/api/utilities/files/css'
import _ from 'lodash'

// import { codemirror } from 'vue-codemirror'

const siteStore = useStoreSite()

export default {
  name: 'Css',
  components: {
    // codemirror
  },
  data () {
    return {
      cdnUrl: '',
      siteStore,
      defaultProps: {
        label: 'name'
      },
      content: '',
      cmOptions: {
        tabSize: 4,
        mode: 'text/css',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  async created () {
    siteStore.fetchCss()
    await siteStore.fetchCustomCss()
  },
  mounted () {},
  computed: {
    codemirror () {
      return this.$refs.editorCss.codemirror
    }
  },
  methods: {
    async uploadCDN () {
      siteStore.file.name = this.cdnUrl
      siteStore.file.pathFile = this.cdnUrl
      siteStore.file.type = 'External'
      siteStore.file.order = siteStore.css.length + 1

      siteStore.storeCssFile().finally(() => (this.cdnUrl = ''))
    },
    async uploadFile (param) {
      siteStore.loading = true
      const formData = new FormData()
      formData.append('file', param.file)
      formData.append('fileName', param.file.name)
      formData.append('folder', 'site')
      upload(formData)
        .then((response) => {
          siteStore.file.name = param.file.name
          siteStore.file.pathFile = response.data
          siteStore.file.type = 'Internal'
          siteStore.file.order = siteStore.css.length + 1
          siteStore.storeCssFile().then(siteStore.fetchCss())
        })
        .catch((response) => {
          // console.log(response)
          param.onError()
        })
        .finally(() => (siteStore.loading = false))
    },
    handleDrop () {
      siteStore.orderFilesCss()
    },
    allowDrop (draggingNode, dropNode, type) {
      if (type !== 'inner') {
        return true
      }
    },
    updateTree () {
      const cssData = _.cloneDeep(siteStore.css)
      siteStore.updateCssFiles(cssData).then(siteStore.fetchCss())
    },
    deleteFile (node, data) {
      siteStore.deleteCssFile(node, data)
    },
    saveCustomCss () {
      const customData = _.cloneDeep(this.customContentCss)
      siteStore.updateCustomCss(customData)
    }
  }
}
</script>

<style>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1em;
  padding-right: 8px;
}
</style>
