<template>
  <div class="container-fluid" v-loading="siteStore.loading" element-loading-text="Cargando...">
    <div class="row">
      <div class="col-6 d-flex align-items-center">
        <div class="position-relative border border-1 rounded shadow w-100">
          <p class="text-center border-bottom pb-2">Agrega archivos a tu sitio</p>
          <p class="px-3 fw-bold">
            Arrastra tu archivo zip para agregarlo al sitio
          </p>
          <el-upload name="fileAssets" action="#" class="upload-demo px-3" :show-file-list="false"
            :http-request="uploadFile" drag :before-upload="beforeUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              Suelta tu archivo aquí o <em>haz clic para cargar</em>
            </div>
            <div slot="tip" class="el-upload__tip">
              Solo archivos zip
            </div>
          </el-upload>
          <div v-if="onUpload">
            <div class="position-absolute top-0 bg-dark opacity-75 w-100 h-100"></div>
            <el-progress class="position-absolute top-50 start-50 translate-middle" type="circle"
              :percentage="progress"></el-progress>
          </div>
        </div>
      </div>
      <div class="col-6">
        <div class="border border-1 rounded shadow" v-loading="loadAssets">
          <p class="text-center border-bottom pb-2">
            Archivos cargados en el sitio
          </p>
          <h4 class="text-center">Path: <el-tag effect="plain"> {{ siteStore.assets[0].path}} </el-tag>
          </h4>
          <el-tabs class="overflow-auto" tab-position="left" style="height: 15rem;">
            <el-tab-pane v-for="directory in siteStore.assets" :key="directory.name" :label="directory.name">
              <el-card class="box-card">
                <div v-if="directory.name != '/'">
                  <el-button class="d-block mx-auto mb-3" type="danger" round size="mini"
                    @click="removeFolder(directory.path)">Eliminar folder</el-button>
                </div>
                <div v-for="folder in directory.folders" :key="folder" class="folder-item">
                  {{ folder }}
                  <el-tooltip class="item float-end" effect="dark" content="Abrir" placement="top">
                    <el-link icon="el-icon-folder-opened" class="link-button" type="info"
                      @click="() => openFolder(directory.path, folder)"></el-link>
                  </el-tooltip>
                </div>
                <div v-for="file in directory.files" :key="file" class="text item">
                  {{ file }}
                  <el-tooltip class="item float-end" effect="dark" content="Eliminar" placement="top">
                    <el-link icon="el-icon-delete" class="link-button" type="danger"
                      @click="() => removeFile(directory.path, file)"></el-link>
                  </el-tooltip>
                </div>
              </el-card>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useStoreSite } from '../../../stores/siteStores'
import { Message } from 'element-ui'
import { upload, removeFolder, removeFile } from '@/api/utilities/files/assets'

const siteStore = useStoreSite()

export default {
  name: 'Settings',
  data () {
    return {
      siteStore,
      tabPosition: 'left',
      defaultProps: {
        label: 'name'
      },
      loadAssets: false,
      file: null,
      chunks: [],
      uploaded: 0,
      sizeChunk: 1000000, // bytes
      progress: 0,
      onUpload: false
    }
  },
  async created () {
    siteStore.fetchAssets()
  },
  watch: {
    chunks (n, o) {
      if (n.length > 0) {
        this.uploadParts(n.length)
      }
    }
  },
  methods: {
    async uploadFile (param) {
      this.file = param.file
      this.createChunks()
      this.onUpload = true
      /* siteStore.loading = true
      const formData = new FormData()
      formData.append('zip', param.file)
      await upload(formData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })
          siteStore.fetchAssets()
        })
        .catch((response) => {
          param.onError()
        })
        .finally(() => (siteStore.loading = false)) */
    },
    async uploadParts (length) {
      const formData = new FormData()
      formData.set('is_last', this.chunks.length === 1)
      formData.set('file', this.chunks[0], `${this.file.name}`)
      await upload(formData)
        .then((response) => {
          console.log(response)
          if (!response.success) {
            this.$message.error(response.messages[0] || 'Error')
            this.chunks = []
            this.progress = 0
            this.uploaded = 0
            this.onUpload = false
          } else {
            this.uploaded += this.chunks[0].size
            this.progress = Math.floor((this.uploaded * 100) / this.file.size)
            if (this.chunks.length === 1) {
              Message({
                showClose: true,
                message: response.messages[0] || 'Exito',
                type: 'success'
              })
              this.progress = 0
              this.uploaded = 0
              this.onUpload = false
              siteStore.fetchAssets()
            }
            this.chunks.shift()
          }
        })
        .catch((response) => {
          this.$message.error('Oops, algo a salido mal. Intenta de nuevo.')
        })
    },
    createChunks () {
      var chunks = Math.ceil(this.file.size / this.sizeChunk)

      for (let i = 0; i < chunks; i++) {
        this.chunks.push(this.file.slice(
          i * this.sizeChunk, Math.min(i * this.sizeChunk + this.sizeChunk, this.file.size), this.file.type
        ))
      }
    },
    async removeFile (path, file) {
      siteStore.loading = true
      const formData = new FormData()
      formData.append('path', path + '/' + file)
      removeFile(formData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })
          siteStore.fetchAssets()
        })
        .catch((response) => {})
        .finally(() => (siteStore.loading = false))
    },
    async removeFolder (path) {
      siteStore.loading = true
      const formData = new FormData()
      formData.append('path', path)
      removeFolder(formData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })
          siteStore.fetchAssets()
        })
        .catch((response) => {})
        .finally(() => (siteStore.loading = false))
    },
    beforeUpload (file) {
      // console.log(file)
      const isZIP = file.type === 'application/zip'
      const isZIP2 = file.type === 'application/x-zip-compressed'
      var flag = true

      if (!isZIP && !isZIP2) {
        this.$message.error('¡El archivo debe ser un ZIP!')
        flag = false
      }
      return flag
    },
    openFolder (path, folder) {
      console.log(path)
      this.loadAssets = true
      const formData = new FormData()
      formData.append('path', path)
      formData.append('folder', folder)
      siteStore.fetchAssets(formData)
        .finally(this.loadAssets = false)
    }
  }
}
</script>
<style>

.el-upload--text {
  width: 100%;
}
.el-upload-dragger {
  width: 100%;
}
.el-progress__text{
  font-size: 1.5rem !important;
  font-weight: 700;
  color: rgb(32, 160, 255);
}
</style>
