<template>
  <el-row :gutter="10" type="flex" justify="center">
    <el-col :offset="1" :xs="22" :sm="22" :md="16" :lg="16" :xl="16">
      <el-row type="flex" class="text-color-gray">
        <el-col :span="12">
          <label style="padding-left: 10px">Editar:</label>&nbsp;&nbsp;
          <el-switch
            v-model="isUpdateForm"
            :disabled="!siteStore.seo"
          ></el-switch>
        </el-col>
        <el-col :span="12" :offset="15">
          <template>
            <el-select
              v-model="configStore.langSelected"
              style="float: right"
              placeholder="Idioma"
              @change="onChangeLang"
            >
              <el-option
                v-for="item in configStore.langs"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </el-col>
      </el-row>
      <div style="margin: 20px"></div>
      <el-card class="box-card" v-if="!isUpdateForm">
        <div class="text item">
          <label class="text-color-gray"
            >Título:
            <span v-if="siteStore.seo.title">{{
              siteStore.seo.title
            }}</span> </label
          ><br />
          <label class="text-color-gray"
            >Descripción:
            <span v-if="siteStore.seo.description">{{
              siteStore.seo.description
            }}</span></label
          ><br />
          <label class="text-color-gray"
            >MetaTags:
            <span v-if="siteStore.seo.description">{{
              siteStore.seo.metaTag
            }}</span></label
          ><br />
          <label class="text-color-gray"
            >Schema:
            <span v-if="siteStore.seo.schema">{{
              siteStore.seo.schema
            }}</span></label
          ><br />
          <label class="text-color-gray">Imagen:</label><br />
          <img
            v-if="siteStore.seo.image"
            :src="configStore.baseUrl + siteStore.seo.image"
            class="image-seo"
          />
          <div v-else>
            <span>Sin imagen</span>
          </div>
        </div>
      </el-card>
      <div style="margin-top: 20px" v-if="isUpdateForm">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <strong><span>Agregar SEO</span></strong>
          </div>
          <div class="text item">
            <ValidationObserver ref="form" v-slot="{ invalid }">
              <el-form
                label-position="top"
                label-width="120px"
                :model="siteStore.seo"
              >
                <el-form-item label="Título">
                  <ValidationProvider
                    name="título"
                    rules="required|min:4|max:60"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      show-word-limit
                      minlength="4"
                      maxlength="60"
                      v-model="siteStore.seo.title"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Descripción">
                  <ValidationProvider
                    name="descripción"
                    rules="required|min:10|max:160"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      type="textarea"
                      :rows="2"
                      minlength="10"
                      maxlength="160"
                      show-word-limit
                      v-model="siteStore.seo.description"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Imagen">
                  <el-upload
                    class="avatar-uploader"
                    drag
                    name="image"
                    action="#"
                    :show-file-list="false"
                    :http-request="uploadImage"
                  >
                    <img
                      v-if="siteStore.seo.image"
                      :src="configStore.baseUrl + siteStore.seo.image"
                      alt=""
                    />
                    <template v-else>
                      <i class="el-icon-upload"></i>
                      <div class="el-upload__text">
                        Suelta tu foto aquí o
                        <em>haz clic para cargar</em>
                      </div>
                    </template>
                    <div slot="tip" class="el-upload__tip">
                      Solo archivos jpg con una medida de 800X800 pixeles
                    </div>
                  </el-upload>
                </el-form-item>
                <el-form-item label="Tags">
                  <ValidationProvider
                    name="Tags"
                    rules="required"
                    v-slot="{ errors }"
                  >
                    <el-select
                      size="mini"
                      v-model="siteStore.seo.metaTag"
                      multiple
                      filterable
                      allow-create
                      placeholder="Tags"
                    >
                      <el-option
                        v-for="itemT in siteStore.optionsTags"
                        :key="itemT.value"
                        :label="itemT.label"
                        :value="itemT.value"
                      >
                      </el-option>
                    </el-select>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item label="Schema">
                  <ValidationProvider
                    name="schema"
                    rules="required"
                    v-slot="{ errors }"
                  >
                    <el-input
                      size="mini"
                      show-word-limit
                      v-model="siteStore.seo.schema"
                    ></el-input>
                    <span>{{ errors[0] }}</span>
                  </ValidationProvider>
                </el-form-item>
                <el-form-item>
                  <el-button
                    @click.stop.prevent="checkSave"
                    type="primary"
                    size="mini"
                    icon="el-icon-upload2"
                    :disabled="invalid"
                  >
                    {{
                      siteStore.isSeoUpdate ? "Actualizar" : "Guardar"
                    }}</el-button
                  >
                </el-form-item>
              </el-form>
            </ValidationObserver>
          </div>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { useStoreSite } from '../../../stores/siteStores'
import { store as storeImageFile } from '@/api/images'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const siteStore = useStoreSite()
const configStore = useStoreConfig()

export default {
  name: 'Seo',
  data () {
    return {
      isUpdateForm: false,
      siteStore,
      configStore
    }
  },
  created () {
    siteStore.fetchSeo()
  },
  methods: {
    async uploadImage (param) {
      siteStore.loading = true
      const formData = new FormData()
      formData.append('image', param.file)
      formData.append('width', '250')
      formData.append('height', '250')
      formData.append('title', 'Image')
      formData.append('alt', 'ciberseguridadSeo')
      storeImageFile(formData)
        .then(response => {
          siteStore.seo.image = response.data.path
          param.onSuccess(response.data.path)
        })
        .catch(response => {
          // console.log(response)
          param.onError()
        })
        .finally(() => (siteStore.loading = false))
    },
    onChangeLang (lang) {
      this.configStore.setLangSelected(lang)
      siteStore.fetchSeo()
    },
    checkSave () {
      if (siteStore.isSeoUpdate) {
        const seoData = _.cloneDeep(siteStore.seo)
        const lang = _.cloneDeep(configStore.langSelected)
        siteStore.updateSeo(seoData, lang).then(() => {
          this.isUpdateForm = false
          // siteStore.fetchSeo()
        })
      } else {
        siteStore.seo.lang = configStore.langSelected
        siteStore.storeSeo().then(() => {
          this.isUpdateForm = false
          // siteStore.fetchSeo()
        })
      }
    }
  }
}
</script>
<style scoped>
.text-color-gray {
  color: #606266;
}
.image-seo {
  width: 100px;
  height: 100px;
}
.btn-js {
  background-color: #fff458;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
.btn-js:hover {
  background-color: #f0e985;
  color: rgb(5, 5, 5);
  border-color: #fff458;
}
</style>
